.formulario {
	display: flex;
	justify-content: space-evenly;
	margin: 80px 0;
	flex-wrap: wrap;
	margin: 80px 50px;
	gap: 20px;
}

.formulario form {
	/* max-width: 80%; */
	background: #f7f7f7;
	box-shadow: 7px 7px 15px rgba(0, 0, 0, 25);
	border-radius: 20px;
	padding: 8px 100px;
	flex: 1;
}

.formulario h2 {
	font-size: 32px;
	font-style: 400;
	color: #212121;
}
