.campo {
	margin: 24px 0;
	font-family: "Montserrat", sans-serif;
}
.campo label {
	font-family: "Montserrat", sans-serif;
	font-size: 18px;
	font-weight: 600;
	display: block;
	margin-bottom: 8px;
}

.campo input {
	width: 100%;
	background: #ffffff;
	box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.06);
	border: none;
	padding: 30px 20px;
	outline-color: #6278f7;
	font-size: 16px;
	box-sizing: border-box;
}

.campo-color input {
	height: 40px;
	padding: 5px 20px;
}
