.equipo {
	width: 100%;
	/* background: #d9f7e9; */
	padding: 32px;
	box-sizing: border-box;
	text-align: center;
	font-size: 32px;
	position: relative;
}

.equipo h3 {
	font-weight: 400;
	color: #212121;
	border-bottom: 4px solid #57c278;
	display: inline-block;
	padding-bottom: 8px;
}

.colaboradores {
	display: flex;
	justify-content: space-evenly;
	flex-wrap: wrap;
}

.inputColor {
	position: absolute;
	top: 20px;
	right: 50px;
}
