.footer {
	width: 100%;
	display: flex;
	padding: 5%;
	align-items: center;
	box-sizing: border-box;
	background-size: cover;
	background-position: center;
	justify-content: space-between;
	color: #fff;
	font-family: "Montserrat", sans-serif;
	font-size: 20px;
}

.footer img {
	padding: 0 15px;
}
